.text__gradient {
  background: linear-gradient(90deg, #8F52F3 3.43%, #00AAE1 97.98%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main__btn {
  background: linear-gradient(90deg, #8F52F3 3.43%, #00AAE1 97.98%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  padding: rem(14) rem(35);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  border-radius: 13px 0 13px 0;
  box-shadow: 0px 0px 50px rgba(100, 109, 238, 0.10);
  transition: all 0.5s;
  line-height: normal;

  &::before {
    content: "";
    background: linear-gradient(90deg, #00AAE1 3.43%, #8F52F3 97.98%);
    position: absolute;
    bottom: 0px;
    right: 0px;
    left: 0px;
    top: 0px;
    z-index: -2;
    border-radius: 13px 0 13px 0;
  }

  &::after {
    content: "";
    opacity: 0.9;
    background: #07071C;
    position: absolute;
    bottom: 1px;
    right: 1px;
    left: 1px;
    top: 1px;
    z-index: -1;
    border-radius: 12.5px 0 12.5px 0;
    transition: all 0.5s;

  }

  &:hover {
    background: #FFF;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    &::after {
      opacity: 0;
    }
  }
}

img {
  max-width: 100%;
  height: auto;
}